* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
    box-sizing: border-box;
}


html{
    -ms-scroll-behavior: smooth;
    -webkit-scroll-behavior: smooth;
    scroll-behavior: smooth;
}


body{
    font-family: Calibri, Helvetica, sans-serif;
    color: white;  
}






#grid{
    display:grid;
    grid-template:  6vh repeat(auto-fill, 1fr)/ repeat(auto-fill, minmax(400px, 1fr));
    overflow: hidden;
    
    
}

#header{
    z-index: 1;
    position: fixed;
    width: 100%;
    overflow: hidden;
    background-color: black;
}

.title{
    font-size: 5vh;
    font-weight: 700;
    margin-left: 1vh;
    width: auto;
    float: left;  
    color:white; 
}
.title:hover{
    text-decoration: underline;
    cursor: pointer;
}

.navlistItems {
    font-weight: 600;
    font-size: 3vh;
    margin: 1vh 1vw;
    list-style : none;
    color: white;
}
.navlistItems:hover, .collageTitle:hover, .titleSubpage:hover{
    text-decoration: underline;
    cursor: pointer;
}

.titleSubpage:hover{
    color: greenyellow;
}

.navlist{
    float: right;
    display: flex;
}

/*MAIN PICTURE OF THE INDEX PAGE*/
.mainContent{

    position: relative;
}

#mainDisplay{
    display: flex;
    grid-area: 2/1/span 16/-1;
    position: relative;
    min-height: 100vh;
    background-image: url('/static/images/Picture1.png');
    background-size: 100% 100%;
    overflow: hidden;
    justify-content: center;
}
 

#titleTextBtn{
    display: flex;
    margin-top: 45vh;
    margin-left: 30vw;
    background-color: black;
    border-radius: 10px;
    background-color:  rgb(0, 0,0, 0.7);
    border: black solid 7px;
    overflow: hidden;
    width: 40vw; 
    max-height: 15vh;
    font-size: 5vh;
    font-size: min(6vh, 6vw);
    font-weight: 1000;
    color: white;
    text-align: center;
    align-items: center;
    justify-content: center;
}

#titleTextBtn:hover{
    cursor: pointer;
    border: yellowgreen solid 7px;
    background-color: rgb(0,0,0,0.8);
}

/* Second in the index*/
#aboutSummary{
    min-height: 100vh;
    display: flex;
    grid-area: 18/1/ span 17 / -1;
    background-image: url('/static/images/About.jpg');
    justify-content: center;
    color: white;
   flex-wrap: wrap;
}



.titleSubpage, .subtitleSubpage{
    color: white;
    font-weight: 700;
    display: flex;
    align-self: center;
    justify-content: center;
    flex-wrap: wrap;
}

.titleSubpage{
    margin-top: 6vh;
    margin-bottom: 2vh;
    font-size:6vh;
    font-size: min(6vh, 6vw);
    
}
.subtitleSubpage, .thumbTitle{
    font-size: 3vh;
    font-size: min(3vh, 3vw);
    font-weight: 700;
    margin-bottom: 4vh;
}




.textSubpage{
    max-width: 90vw;
    font-family: Calibri,sans-serif, Arial;
    letter-spacing: 0.1vw;
    font-size: 2.5vh;
    
}



.textSubpageListItem{
    display: flex;
    /*border-left: white solid 1px;*/
    width: 30%;
    min-width: 300px;
    margin: 12vh 1.5vw;
    align-items: center;
    text-align: center;
    justify-content:center;
    -moz-animation: wobble 6s  infinite;
    animation: wobble 6s  infinite;
}



@keyframes shiftUpDown {
    0%{
        transform: translateY(0px);
    }
    25%{
        transform: translateY(3px);
    }
    50%{
        transform: translateY(0px);
    }
    75%{
        transform: translateY(-3px) ;
    }
    100%{
        transform: translateY(0px) ;
    }
}


/*Projects Summary : 3rd item on the main page*/
#projectSummary{
    grid-area: 35 / 1 / span 25 / -1;
    min-height: 100vh;
    justify-content: center;
    background-image: url('/static/images/Projects.jpg');
    background-size: cover;
}

.collageElements{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;  

}

.canvasContainer{
    display: block;
    
    margin-left: 3vw;
}
