.collageGrid{
    display: grid;
    grid-template: 1fr 7fr / 100%;
    justify-content: center;
    margin: 5vh 3vw;

}

a{
    color:white;
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}



.collageTitle{
    font-size: 3vh;
    font-size: min(3vh, 3vw);
    font-weight: 700;
    margin-bottom: 1vh;
    grid-area: 1 / 1/ span 1 / span 1;
    justify-self: center;
}


.collageImg{
    grid-area: 2 /1 / span 1 / span 1;
    -ms-max-width:400px;
    height: 33vh;
    max-width: 300px;
    justify-self: center;
    border-bottom: 5px solid aquamarine;
}

.collageImg:hover{
    border-bottom: 5px solid white;
}

/*Contact Summary*/
#contact{
    grid-area: 60 /1 /span 8/-1;
    background-image: url('/static/images/Contact.jpg');
    background-size: 100% 300%;
    display: flex;
    justify-content: center;
    padding-bottom: 6vh;
}
#contact:hover{
    color:white;
    text-decoration: none;
}


/*Scrollbar*/
::-webkit-scrollbar{
    width: 20px;
}

::-webkit-scrollbar-track{
    box-shadow: inset 0 0 5px black; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb{
    background: darkred;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover{
    background: red;
    border-radius: 10px;
}




